
.self{position:fixed;width:100%;height:100%;background-image:url("http://ofyaji162.bkt.clouddn.com/bg2.jpg");background-position:50%;background-repeat:no-repeat;background-size:cover
}
.m-box{position:absolute;width:620px;height:540px;top:50%;margin-top:-270px;left:50%;margin-left:-310px;background:#fff;-webkit-border-radius:10px;border-radius:10px;opacity:0.9;-webkit-animation:mBox 0.5s ease-out 1;animation:mBox 0.5s ease-out 1;-webkit-transition:opacity 0.5s ease;transition:opacity 0.5s ease
}
@-webkit-keyframes mBox{
0%{opacity:0;-webkit-transform:translate3d(-10px, 0, 0);transform:translate3d(-10px, 0, 0)
}
25%{-webkit-transform:translate3d(10px, 0, 0);transform:translate3d(10px, 0, 0)
}
50%{-webkit-transform:translate3d(-10px, 0, 0);transform:translate3d(-10px, 0, 0)
}
75%{-webkit-transform:translate3d(10px, 0, 0);transform:translate3d(10px, 0, 0)
}
100%{opacity:0.9
}
}
@keyframes mBox{
0%{opacity:0;-webkit-transform:translate3d(-10px, 0, 0);transform:translate3d(-10px, 0, 0)
}
25%{-webkit-transform:translate3d(10px, 0, 0);transform:translate3d(10px, 0, 0)
}
50%{-webkit-transform:translate3d(-10px, 0, 0);transform:translate3d(-10px, 0, 0)
}
75%{-webkit-transform:translate3d(10px, 0, 0);transform:translate3d(10px, 0, 0)
}
100%{opacity:0.9
}
}
.box-head{position:relative;width:100%;height:65%;background:url(http://ofyaji162.bkt.clouddn.com/bg1.jpg) center no-repeat;background-size:cover;-webkit-border-radius:4px 4px 0 0;border-radius:4px 4px 0 0;text-align:center;-webkit-box-shadow:inset 0 -70px 100px -50px rgba(0,0,0,0.5);box-shadow:inset 0 -70px 100px -50px rgba(0,0,0,0.5)
}
.box-head .box-name{font-size:25px;padding-top:20%;color:white
}
.box-content{font-size:15px;padding:8px 5%;text-align:center
}
.box-content p{line-height:30px
}
.box-content .title{font-size:25px;font-weight:bold;line-height:50px;text-align:center
}
@media screen and (max-width: 640px){
.m-box{top:60%;margin-left:-49%;width:98%;height:400px
}
.box-head{height:50%
}
.box-content{padding:8px 5px;font-size:14px
}
.box-content p{text-align:left
}
}



