
.friends{min-height:1000px;background-image:url("http://ofyaji162.bkt.clouddn.com/bg1.jpg");background-position:50%;background-repeat:no-repeat;background-size:cover;overflow-x:hidden
}
.friends .title{margin:50px;font-size:20px;color:rgba(255,255,255,0.9)
}
.friends .f-list{position:relative;height:auto;margin:auto
}
.friends .f-item{position:relative;width:20%;height:300px;-webkit-border-radius:50%;border-radius:50%;display:inline-block
}
.friends .f-item .avatar{position:absolute;top:0;left:50%;margin-left:-60px;display:block;width:120px;height:120px;-webkit-border-radius:50%;border-radius:50%;background-repeat:no-repeat;background-size:cover;background-color:rgba(255,255,255,0.2);z-index:9
}
.friends .f-item .line{position:absolute;top:115px;left:50%;margin-left:-5px;width:5px;height:90px;-webkit-border-radius:20%;border-radius:20%;border-right:2px solid rgba(128,64,0,0.9);z-index:1;cursor:pointer
}
.friends .f-item .line1{-webkit-transition:-webkit-transform 0.2s linear;transition:-webkit-transform 0.2s linear;transition:transform 0.2s linear;transition:transform 0.2s linear, -webkit-transform 0.2s linear
}
.friends .f-item .des{position:absolute;top:198px;left:50%;margin-left:-90px;width:180px;background-color:white;color:black;-webkit-border-radius:10px;border-radius:10px;padding:5px;font-size:14px;-webkit-backface-visibility:hidden;backface-visibility:hidden;z-index:2;-webkit-transition:all 0.2s linear;transition:all 0.2s linear
}
.friends .f-item .des .name{font-size:16px;text-align:center;line-height:30px
}
@media screen and (max-width: 1400px){
.friends .f-item{width:25%
}
}
@media screen and (max-width: 1100px){
.friends .f-item{width:33%
}
}
@media screen and (max-width: 670px){
.friends .f-item{width:50%
}
.friends .f-item .des{width:130px;margin-left:-70px
}
}



