
.n-news{position:fixed;width:10%;height:100%;overflow-y:auto;margin:2%
}
.n-news::-webkit-scrollbar{width:4px;height:4px;background-color:#202020;-webkit-border-radius:8px;border-radius:8px
}
.n-news::-webkit-scrollbar-thumb{background-color:rgba(235,87,86,0.73);-webkit-border-radius:8px;border-radius:8px
}
.n-news .n-item{display:block;width:110px;height:40px;line-height:40px;text-align:center;color:#444;-webkit-border-radius:4px;border-radius:4px;margin-bottom:2px;-webkit-transition-property:color,background-color;transition-property:color,background-color;cursor:pointer
}
.n-news .n-item:hover{background-color:#ed4040;color:#fff
}
.n-news .activity{background-color:#ed4040;color:#fff
}
.news-detail{float:right;width:85%;margin-top:2%
}
.news-detail .d-item{cursor:pointer
}
.news-detail .d-item a.photo span{background-image:url("http://ojdfj29yl.bkt.clouddn.com/news.jpg");background-size:cover
}
.news-detail .n-more{width:100%;display:block;margin:auto
}
.news-body{width:80%;padding:10px;margin:auto
}
.news-body p{font-size:16px;line-height:30px
}
.news-body p:first-child{font-size:23px;line-height:60px;text-align:center
}
.news-body img{display:block;margin:10px auto;max-width:100%
}
@media screen and (max-width: 600px){
.n-news{position:relative;float:left;width:100%;margin:3px
}
.n-news .n-item{float:left
}
.news-detail{width:100%
}
}



