
.btn{line-height:1;white-space:nowrap;cursor:pointer;background:#fff;border:1px solid #c0ccda;color:#1f2d3d;-webkit-appearance:none;text-align:center;-webkit-box-sizing:border-box;box-sizing:border-box;outline:none;margin:0;-moz-user-select:none;-webkit-user-select:none;-ms-user-select:none;padding:9px 15px;font-size:14px;-webkit-border-radius:4px;border-radius:4px;color:#fff;background-color:#50bfff;border-color:#50bfff;-webkit-transition:all 0.2s ease;transition:all 0.2s ease
}
.btn:hover{opacity:0.8
}
button.disabled{cursor:not-allowed
}
i.i-loading{position:absolute;padding:-2px;-webkit-animation:loading 2s linear infinite;animation:loading 2s linear infinite
}
@-webkit-keyframes loading{
0%{-webkit-transform:rotate3d(0, 0, 0, 360deg)
}
100%{-webkit-transform:rotate3d(0, 0, 1, 360deg)
}
}
@keyframes loading{
0%{-webkit-transform:rotate3d(0, 0, 0, 360deg);transform:rotate3d(0, 0, 0, 360deg)
}
100%{-webkit-transform:rotate3d(0, 0, 1, 360deg);transform:rotate3d(0, 0, 1, 360deg)
}
}
.m-title{width:100%;padding-left:30px;height:100px;background-color:#e3ebec
}
.m-title h3{font-size:35px;font-weight:bold;color:#37a;letter-spacing:1px;line-height:100px
}
.m-list{position:relative;width:1400px;margin:auto
}
.m-list .m-item{position:relative;display:inline-block;width:300px;height:444px;margin:40px 0 0 2%;overflow:hidden;cursor:pointer;-webkit-animation:show-m .5s ease-out 1;animation:show-m .5s ease-out 1
}
@-webkit-keyframes show-m{
0%{opacity:0
}
100%{opacity:1
}
}
@keyframes show-m{
0%{opacity:0
}
100%{opacity:1
}
}
.m-list .m-item .m-img{width:100%;height:100%;-webkit-transform:scale(1.04);-ms-transform:scale(1.04);transform:scale(1.04);-webkit-transfrom:scale(1.04);-moz-webkit-transfrom:scale(1.04)
}
.m-list .m-item .m-top{position:absolute;top:0;left:0;width:100%;height:40px;background-color:rgba(0,0,0,0.4)
}
.m-list .m-item .m-top p{text-align:center;line-height:40px;font-weight:600;color:rgba(255,255,255,0.9)
}
.m-list .m-item .m-bottom{position:absolute;bottom:0;left:0;width:100%;padding:10px;font-size:14px;color:rgba(255,255,255,0.8);background-color:rgba(0,0,0,0.6);-webkit-transform:translate3d(0, 110px, 0);transform:translate3d(0, 110px, 0);-webkit-transition:0.3s all ease-out;transition:0.3s all ease-out;opacity:0.8
}
.m-list .m-item .m-bottom p{width:95%;line-height:26px
}
.m-list .m-item:hover .m-bottom{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
.add-more{display:block;margin:10px auto 50px auto;width:200px;cursor:pointer
}
.movie-bg{position:fixed;top:0;left:250px;width:100%;height:100%;background-color:rgba(0,0,0,0.8);overflow-x:hidden;-webkit-overflow-scrolling:touch
}
.movie-bg .movie-pos{position:relative;display:block;width:100%;margin-left:-125px;padding-top:50px;padding-bottom:100px;background:none
}
.movie-bg .movie-pos .movie-detail{position:relative;width:60%;min-height:800px;margin:auto;-webkit-border-radius:5px;border-radius:5px;background-color:rgba(255,255,255,0.95);-webkit-transition:all .3s ease-out;transition:all .3s ease-out
}
.movie-bg .movie-pos .movie-detail .movie-header{padding:5px 10px;height:40px;border-bottom:1px solid rgba(103,106,108,0.5)
}
.movie-bg .movie-pos .movie-detail .movie-header i{float:right;padding-top:5px;font-size:20px;cursor:pointer
}
.movie-bg .movie-pos .movie-detail .movie-header h4{line-height:40px
}
.movie-bg .movie-pos .movie-detail .movie-body .title{margin-top:20px;text-align:center;font-size:20px;line-height:40px;font-weight:600
}
.movie-bg .movie-pos .movie-detail .movie-body .m-left{float:left;width:30%;margin:25px 2%
}
.movie-bg .movie-pos .movie-detail .movie-body .m-left img{max-width:100%;display:block;margin:auto
}
.movie-bg .movie-pos .movie-detail .movie-body .m-right{display:inline-block;width:65%;margin-top:20px
}
.movie-bg .movie-pos .movie-detail .movie-body .m-right p{padding-left:3px;line-height:30px
}
.movie-bg .movie-pos .movie-detail .movie-body .m-right p span{font-size:14px;padding-left:7px
}
.movie-bg .movie-pos .movie-detail .movie-body .m-right p .btn{font-size:13px;margin-left:10px;padding:1px 2px
}
@media screen and (max-width: 1600px){
.m-list{width:1000px
}
.m-list .m-item{margin-left:3%
}
}
@media screen and (max-width: 1300px){
.m-list{width:800px
}
.m-list .m-item{margin-left:5%
}
}
@media screen and (max-width: 1000px){
.movie-bg{left:0
}
.movie-bg .movie-pos{margin-left:0;margin-top:100px
}
.movie-bg .movie-pos .movie-detail{position:relative;width:80%
}
}
@media screen and (max-width: 850px){
.m-list{width:100%
}
.m-list .m-item{display:block;margin:20px auto
}
.m-list .m-item .m-bottom{opacity:1;transform:translate3d(0, 0, 0);-webkit-transform:translate3d(0, 0, 0);-moz-transform:translate3d(0, 0, 0)
}
.movie-bg .movie-pos{margin-top:10px;width:100%
}
.movie-bg .movie-pos .movie-detail{width:90%
}
.movie-bg .movie-pos .movie-detail .movie-body .m-left{margin:10px 0;width:100%
}
.movie-bg .movie-pos .movie-detail .movie-body .m-right{float:none;margin:auto;width:95%
}
}
@media screen and (max-width: 320px){
.m-title h3{font-size:25px
}
.m-list .m-item{display:block;width:260px;height:350px;margin:20px auto
}
}



