
.vue-waterfall{width:1500px;margin:auto
}
.vue-waterfall .vue-waterfall-slot{padding:2px
}
.vue-waterfall-slot img{width:100%;background-color:rgba(0,0,0,0.1)
}
.l-show{position:fixed;width:100%;height:100%;padding-left:100px;top:0;left:0;background-color:rgba(0,0,0,0.8);z-index:100;cursor:-webkit-zoom-out;cursor:zoom-out;background-position:center center;background-size:cover
}
.l-show .download{display:inline-block;position:absolute;width:100px;margin-left:0px;text-align:center;z-index:999;left:50%;top:10px;color:white
}
.l-show img{display:block;margin:auto;padding-top:50px;max-width:95%;height:80%
}
.t-show{position:fixed;width:100%;height:100%;padding-left:100px;top:0;left:0;z-index:200;cursor:-webkit-zoom-out;cursor:zoom-out;background-position:center center;background-repeat:no-repeat;background-size:cover
}
.t-show img{display:block;margin:auto;padding-top:50px;max-width:95%;height:80%
}
@media screen and (max-width: 1670px){
.vue-waterfall{width:1200px
}
}
@media screen and (max-width: 1470px){
.vue-waterfall{width:900px
}
}
@media screen and (max-width: 1170px){
.vue-waterfall{width:600px
}
}
@media screen and (max-width: 1000px){
.l-show{padding:0
}
.l-show .download{margin-left:-50px;top:60px
}
.l-show img{padding-top:100px
}
.t-show{padding:0px
}
.t-show img{padding-top:100px
}
}
@media screen and (max-width: 600px){
.vue-waterfall{width:100%
}
.vue-waterfall .vue-waterfall-slot{position:relative !important;top:0 !important;margin:1px 0;width:100% !important;height:auto !important
}
.l-show img{width:95%;height:auto
}
.t-show img{width:95%;height:auto
}
}



